:root {
  --speed: .5s;
}

.lobody {
  width: 100vw;
  height: 100vh;
  perspective: 100vmin;
  background: radial-gradient(circle at 50% 100%, #212121, #000);
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
}

.content {
  width: 111vmin;
  height: 0;
  transform: rotateY(55deg)scale3d(.5, 1.5, .5);
}

.cuboid {
  --height: 8;
  --width: 30;
  --depth: 30;
  --hue: 50;
  --sat: 90%;
  height: calc(var(--height) * 1vmin);
  width: calc(var(--width) * 1vmin);
  animation: loading var(--speed) cubic-bezier(.4, 0, 1, 1) calc(calc(var(--speed) / 8) * -1) infinite alternate;
  position: absolute;
}

.cuboid .side {
  height: 100%;
  width: 100%;
  animation: rainbow calc(var(--speed) * 2) linear calc(calc(var(--speed) / 8) * -8) infinite;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes rainbow {
  0%, 10% {
    filter: hue-rotate();
  }

  10.1%, 20% {
    filter: hue-rotate(36deg);
  }

  20.1%, 30% {
    filter: hue-rotate(72deg);
  }

  30.1%, 40% {
    filter: hue-rotate(108deg);
  }

  40.1%, 50% {
    filter: hue-rotate(144deg);
  }

  50.1%, 60% {
    filter: hue-rotate(180deg);
  }

  60.1%, 70% {
    filter: hue-rotate(216deg);
  }

  70.1%, 80% {
    filter: hue-rotate(252deg);
  }

  80.1%, 90% {
    filter: hue-rotate(288deg);
  }

  90.1%, 100% {
    filter: hue-rotate(324deg);
  }
}

.cuboid .side:nth-of-type(1) {
  transform: translate3d(-50%, -50%, calc(var(--depth) * .5vmin) );
  background: hsl(var(--hue), var(--sat), 60%);
}

.cuboid .side:nth-of-type(2) {
  transform: translate3d(-50%, -50%, calc(var(--depth) * -.5vmin) ) rotateY(180deg);
  background: hsl(var(--hue), var(--sat), 25%);
}

.cuboid .side:nth-of-type(3) {
  width: calc(var(--depth) * 1vmin);
  transform: translate(-50%, -50%) rotateY(90deg) translate3d(0, 0, calc(var(--width) * .5vmin) );
  background: hsl(var(--hue), var(--sat), 75%);
}

.cuboid .side:nth-of-type(4) {
  width: calc(var(--depth) * 1vmin);
  transform: translate(-50%, -50%) rotateY(-90deg) translate3d(0, 0, calc(var(--width) * .5vmin) );
  background: hsl(var(--hue), var(--sat), 40%);
}

.cuboid .side:nth-of-type(5) {
  height: calc(var(--depth) * 1vmin);
  transform: translate(-50%, -50%) rotateX(90deg) translate3d(0, 0, calc(var(--height) * .5vmin) );
  background: hsl(var(--hue), var(--sat), 20%);
}

.cuboid .side:nth-of-type(6) {
  height: calc(var(--depth) * 1vmin);
  transform: translate(-50%, -50%) rotateX(-90deg) translate3d(0, 0, calc(var(--height) * .5vmin) );
  background: hsl(var(--hue), var(--sat), 15%);
}

@keyframes loading {
  0% {
    margin-top: 0;
  }

  100% {
    margin-top: 7vmin;
  }
}

.cuboid:nth-child(2) {
  --height: 7;
  animation-delay: calc(calc(var(--speed) / 8) * -2);
  top: 9vmin;
}

.cuboid:nth-child(3) {
  --height: 6;
  animation-delay: calc(calc(var(--speed) / 8) * -3);
  top: 17vmin;
}

.cuboid:nth-child(4) {
  --height: 5;
  animation-delay: calc(calc(var(--speed) / 8) * -4);
  top: 24vmin;
}

.cuboid:nth-child(5) {
  --height: 4;
  animation-delay: calc(calc(var(--speed) / 8) * -5);
  top: 30vmin;
}

.cuboid:nth-child(6) {
  --height: 3;
  animation-delay: calc(calc(var(--speed) / 8) * -6);
  top: 35vmin;
}

.cuboid:nth-child(7) {
  --height: 2;
  animation-delay: calc(calc(var(--speed) / 8) * -7);
  top: 39vmin;
}

.cuboid:nth-child(8) {
  --height: 1;
  animation-delay: calc(calc(var(--speed) / 8) * -8);
  top: 42vmin;
}

.cuboid:nth-child(2) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -9);
}

.cuboid:nth-child(3) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -10);
}

.cuboid:nth-child(4) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -11);
}

.cuboid:nth-child(5) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -12);
}

.cuboid:nth-child(6) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -13);
}

.cuboid:nth-child(7) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -14);
}

.cuboid:nth-child(8) .side {
  animation-delay: calc(calc(var(--speed) / 8) * -15);
}

.cuboid:nth-child(9) {
  --height: 51;
  --width: 32;
  --depth: 32;
  animation: none;
  transform: translateY(-.5vmin)translateX(-1vmin)translateZ(0);
}

.cuboid:nth-child(9) .side {
  opacity: .5;
  filter: hue-rotate(30deg);
  background: rgba(205, 220, 57, .55);
  border: 2px solid #000;
  border-width: 2px 8px 2px 7px;
  box-shadow: inset 0 0 10vmin 2vmin #000, 0 0 1px 1px rgba(0, 0, 0, .533);
}

.cuboid .side:nth-of-type(5), .cuboid .side:nth-of-type(6) {
  border: none !important;
}

